import { Component, Input, OnInit } from "@angular/core";
import { Params } from "@angular/router";
import { IAttPlanShiftList, IPlanDetails } from "app/attendance-setup/models/plan";
import { AttendancePlanService } from "app/attendance-setup/services/attendance-plan.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { HelpersFunctions } from "app/shared/resources/helpers-functions";

@Component({
	selector: "app-plan-details",
	templateUrl: "./plan-details.component.html",
	styleUrls: ["./plan-details.component.scss"],
})
export class PlanDetailsComponent extends AppHelpers implements OnInit {
	@Input() planId: number;
	groupedPlans = {};

	mandatoryColomns: string[] = ["date", "dayName", "is_Basic", "is_WeekEnd"];
	isLoaded: boolean = false;
	optionalColomns: string[] = [];

	constructor(private planServ: AttendancePlanService) {
		super();
	}

	ngOnInit(): void {
		this.activatedRoute.params.subscribe((params: Params) => {
			this.planId = params.planId;
		});
		this.getPlansDetailsById(1);
	}

	getPlansDetailsById(page: number, pageSize?: number) {
		this.page.pageIndex = page;
		if (pageSize) this.page.pageSize = pageSize;
		this.showLoading();
		this.planServ.getDetailsById(this.planId).subscribe((res: IPlanDetails) => {
			if (res?.message?.messageType == MessageTypes.Success) {
				res.data.map((day) => {
					day.dayName = HelpersFunctions.getDayName(new Date(day.date));
					day.date = HelpersFunctions.mapDate(day.date);
				});
				this.groupedPlans = this.groupPlansByIterationNumber(res.data);
			} else {
				this.errorList(res.message.messages);
			}
			this.hideLoading();
		});
	}

	iterationNumberAscending(a, b): number {
		return a.key - b.key;
	}

	groupPlansByIterationNumber(plans: IAttPlanShiftList[]) {
		let grouped = {};
		for (let plan of plans) {
			let iterationNumber = plan.iterationNumber;
			if (!grouped[iterationNumber]) {
				grouped[iterationNumber] = [];
			}
			grouped[iterationNumber].push(plan);
		}
		return grouped;
	}
}
