import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import { IOfficialHolidaysRequest, IOfficialHolidaysModel } from "../models/official-holidays";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { IPageParams } from "app/shared/interfaces/params";

@Injectable({
	providedIn: "root",
})
export class OfficialHolidaysService extends AppClientService {
	constructor() {
		super();
	}

	create(data: IOfficialHolidaysRequest) {
		return this.post<IResponse<IOfficialHolidaysModel>>(
			"AttendanceSetup/OfficialHoliday/Create",
			data
		);
	}

	update(data: IOfficialHolidaysRequest) {
		return this.post<IResponse<IOfficialHolidaysModel>>(
			"AttendanceSetup/OfficialHoliday/Edit",
			data
		);
	}

	getList(params: IPageParams) {
		return this.get<IResponseList<IOfficialHolidaysModel[]>>(
			"AttendanceSetup/OfficialHoliday/GetList",
			{
				params: params,
			}
		);
	}

	getById(id: number) {
		return this.get<IResponse<IOfficialHolidaysModel[]>>("AttendanceSetup/OfficialHoliday/GetAll", {
			params: {
				$filter: `id eq ${id}`,
			},
		});
	}

	getAll() {
		return this.get<IResponse<IOfficialHolidaysModel[]>>("AttendanceSetup/OfficialHoliday/GetAll");
	}

	Delete(id: number) {
		return this.delete<IResponse>("AttendanceSetup/OfficialHoliday/Delete", {
			params: { Id: id },
		});
	}
}
