import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import {
  IAttendanceDeviceModel,
  IAttendanceDeviceRequest,
} from "../models/attendance-device";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { IPageParams } from "app/shared/interfaces/params";

@Injectable({
  providedIn: "root",
})
export class AttendanceDeviceService extends AppClientService {
  constructor() {
    super();
  }

  create(body: IAttendanceDeviceRequest) {
    return this.post<IResponse<IAttendanceDeviceModel>>(
      "AttendanceSetup/AttDevice/Create",
      body
    );
  }

  update(data: IAttendanceDeviceRequest) {
    return this.post<IResponse<IAttendanceDeviceModel>>(
      "AttendanceSetup/AttDevice/Edit",
      data
    );
  }

  getList(params: IPageParams) {
    return this.get<IResponseList<IAttendanceDeviceModel[]>>(
      "AttendanceSetup/AttDevice/GetList",
      { params: params }
    );
  }

  getAll() {
    this.get<IResponse<IAttendanceDeviceModel[]>>(
      "AttendanceSetup/AttDevice/GetAll"
    );
  }

  Delete(id: number) {
    return this.delete<IResponse>("AttendanceSetup/AttDevice/Delete", {
      params: { Id: id },
    });
  }
}
