import { Component, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import {
	IAttendanceDeviceModel,
	IAttendanceDeviceRequest,
} from "app/attendance-setup/models/attendance-device";
import { AttendanceDeviceService } from "app/attendance-setup/services/attendance-device.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CustomValidator } from "app/shared/validators/custom.validator";

@Component({
	selector: "app-attendance-device",
	templateUrl: "./attendance-device.component.html",
	styleUrls: ["./attendance-device.component.scss"],
})
export class AttendanceDeviceComponent extends AppHelpers implements OnInit {
	form: FormGroup;
	submitted: boolean = false;
	mandatoryColomns: string[] = ["id", "name", "machineIP", "actions"];
	isLoaded: boolean = false;
	optionalColomns: string[] = [];
	dataSource: MatTableDataSource<IAttendanceDeviceModel>;
	constructor(private attendanceDeviceService: AttendanceDeviceService) {
		super();
	}

	ngOnInit(): void {
		// if (this.businessId > 0 && this.companyId > 0)
		//   this.getList(this.currentPage);

		this.form = this.fb.group({
			id: [0, { nonNullable: true }],
			name: [null, [Validators.required, CustomValidator.whiteSpace]],
			machineIP: [null, [Validators.required, CustomValidator.whiteSpace]],
		});
	}

	get formCtrls() {
		return this.form.controls;
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.dataSource?.data?.length > 0) this.dataSource = new MatTableDataSource(null);
		if (this.checkNoBusinessOrCompany()) this.getList(this.firstPage);
	}

	getList(page: number, pageSize?: number) {
		this.page.pageIndex = page;
		if (pageSize) this.page.pageSize = pageSize;
		let params: IPageParams = {
			PageNumber: page,
			pageSize: this.page.pageSize,
		};
		this.attendanceDeviceService
			.getList(params)
			.subscribe((res: IResponseList<IAttendanceDeviceModel[]>) => {
				if (res.message.messageType == MessageTypes.Success) {
					this.dataSource = new MatTableDataSource(res.data);
					this.isLoaded = true;
					this.page.length = res.meta.totalItemCount;
				} else this.errorList(res.message.messages);
			});
	}

	submit() {
		this.submitted = true;
		if (this.form.valid) {
			const model: IAttendanceDeviceRequest = {
				ID: this.form.value.id,
				Name: this.form.value.name,
				MachineIP: this.form.value.machineIP,
			};

			model.ID == 0 ? this.create(model) : this.update(model);
		}
	}

	create(model: IAttendanceDeviceRequest) {
		this.showLoading();
		this.attendanceDeviceService
			.create(model)
			.subscribe((res: IResponse<IAttendanceDeviceModel>) => {
				if (res?.message?.messageType == MessageTypes.Success) {
					this.clearForm();
					this.getList(this.lastPage, this.page.pageSize);
					this.addSuccessfullyMsg(res?.message?.messageTypeName);
				} else this.errorList(res.message.messages);
				this.hideLoading();
			});
	}

	update(model: IAttendanceDeviceRequest) {
		this.showLoading();
		this.attendanceDeviceService
			.update(model)
			.subscribe((res: IResponse<IAttendanceDeviceModel>) => {
				if (res.message.messageType == MessageTypes.Success) {
					this.clearForm();
					this.getList(this.currentPage, this.page.pageSize); // current page after update
					this.updateSuccessfullyMsg(res?.message?.messageTypeName);
				} else this.errorList(res.message.messages);
				this.hideLoading();
			});
	}

	onDelete(device: IAttendanceDeviceModel): void {
		this.deleateDialog(device.name).subscribe((confirm) => {
			if (confirm) this.delete(device);
		});
	}

	delete(device: IAttendanceDeviceModel) {
		this.showLoading();
		this.attendanceDeviceService.Delete(device.id).subscribe((res: IResponse) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
				if (this.dataSource.data.length == 1 && this.currentPage > 1) {
					// if last item in list: get previous page
					this.getList(this.prevPage, this.page.pageSize);
				} else {
					// if not last item in list: get current page
					this.getList(this.currentPage, this.page.pageSize);
				}
				if (device.id == this.formCtrls.id.value) this.clearForm();
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	attendanceDeviceTOEdit(attendanceDevice: IAttendanceDeviceModel) {
		this.form.patchValue(attendanceDevice);
	}

	clearForm() {
		this.form.reset({ id: 0 });
		this.submitted = false;
	}
}
