import { Component, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { Params } from "@angular/router";
import {
	IPermissionTypeDropdownResponse,
	IPermissionTypeModel,
	IPermissionTypeRequest,
} from "app/attendance-setup/models/permissions";
import { PermissionTypeService } from "app/attendance-setup/services/permissions.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CustomValidator } from "app/shared/validators/custom.validator";
import { CustomValidators } from "ngx-custom-validators";

@Component({
	selector: "app-add-permission",
	templateUrl: "./add-permission.component.html",
	styleUrls: ["./add-permission.component.scss"],
})
export class AddPermissionComponent extends AppHelpers implements OnInit {
	permissionId: number = 0;
	form: FormGroup;
	submitted: boolean = false;
	constructor(private permissionTypeService: PermissionTypeService) {
		super();
	}

	ngOnInit(): void {
		this.activatedRoute.params.subscribe((params: Params) => {
			this.permissionId = params.permissionId ?? 0;
		});

		// if (this.businessId > 0 && this.companyId > 0 && this.permissionId > 0) {
		//   this.getPermissionById();
		// }
		this.form = this.fb.group(
			{
				id: [0, { nonNullable: true }],
				name: [null, [Validators.required, CustomValidator.whiteSpace]],
				code: [null, [Validators.required, CustomValidator.whiteSpace]],
				new_Hired_Month_Indecator: [null, [Validators.required, CustomValidators.gt(0)]],
				num_Permission_Per_Year_New_Hired: [null, [Validators.required, CustomValidators.gt(0)]],
				num_Permission_Per_Month: [null, [Validators.required, CustomValidators.gt(0)]],
				num_Permission_Per_Year: [null, [Validators.required, CustomValidators.gt(0)]],
				total_Hour_Per_Month: [null, [Validators.required, CustomValidators.gt(0)]],

				total_Hour_Per_Permission: [null, [Validators.required, CustomValidators.gt(0)]],
				enable_Validation: [false, [Validators.required]],
				warningInsertAction: [false, [Validators.required]],
				stopInsertAction: [false, [Validators.required]],
			},
			{
				validators: [
					CustomValidator.greaterThan("num_Permission_Per_Month", "num_Permission_Per_Year"),
					CustomValidator.greaterThan("total_Hour_Per_Permission", "total_Hour_Per_Month"),
				],
			}
		);
	}

	get formCtrls() {
		return this.form?.controls;
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.checkNoBusinessOrCompany()) this.getPermissionById();
	}

	submit() {
		this.submitted = true;
		if (this.form.valid) {
			const model: IPermissionTypeRequest = {
				id: this.form.value.id,
				name: this.form.value.name,
				code: this.form.value.code,
				new_Hired_Month_Indecator: this.form.value.new_Hired_Month_Indecator,
				num_Permission_Per_Year_New_Hired: this.form.value.num_Permission_Per_Year_New_Hired,
				enable_Validation: this.form.value.enable_Validation,
				warningInsertAction: this.form.value.warningInsertAction,
				stopInsertAction: this.form.value.stopInsertAction,
				num_Permission_Per_Month: this.form.value.num_Permission_Per_Month,
				num_Permission_Per_Year: this.form.value.num_Permission_Per_Year,
				total_Hour_Per_Month: this.form.value.total_Hour_Per_Month,
				total_Hour_Per_Permission: this.form.value.total_Hour_Per_Permission,
			};
			model.id == 0 ? this.create(model) : this.update(model);
		}
	}

	enableValidation(val) {
		if (val) {
			this.formCtrls.warningInsertAction.setValue(true);
			this.formCtrls.stopInsertAction.setValue(false);
		} else {
			this.formCtrls.warningInsertAction.setValue(false);
			this.formCtrls.stopInsertAction.setValue(false);
		}
	}

	radioChange(val) {
		this.formCtrls.warningInsertAction.setValue(val);
		this.formCtrls.stopInsertAction.setValue(!val);
	}

	create(model: IPermissionTypeRequest) {
		this.showLoading();
		this.permissionTypeService.create(model).subscribe((res: IResponse<IPermissionTypeModel>) => {
			if (res?.message?.messageType == MessageTypes.Success) {
				this.addSuccessfullyMsg(res?.message?.messageTypeName);
				let url = `/attendance-setup/${this.moduleId}/permissions/edit/${res.data.id}`;
				this.navigateTo(url);
				this.permissionId = res.data.id;
				this.formCtrls.id.setValue(res.data.id);
			} else this.errorList(res?.message?.messages);
			this.hideLoading();
		});
	}

	getPermissionById() {
		this.permissionTypeService
			.getById(this.permissionId)
			.subscribe((res: IPermissionTypeDropdownResponse) => {
				if (res.message.messageType == MessageTypes.Success) {
					let permission = res.data[0];
					this.form.patchValue(permission);
				}
			});
	}

	update(model: IPermissionTypeRequest) {
		this.showLoading();
		this.permissionTypeService.update(model).subscribe((res: IResponse<IPermissionTypeModel>) => {
			if (res?.message?.messageType == MessageTypes.Success) {
				this.updateSuccessfullyMsg(res?.message?.messageTypeName);
			} else this.errorList(res?.message?.messages);
			this.hideLoading();
		});
	}

	clearForm() {
		this.form.reset({ id: 0, enable_Validation: false });
		this.submitted = false;
	}
}
