import { Component, Injector, OnInit } from "@angular/core";
import { IAttendancePlanRequest } from "app/attendance-setup/models/plan";
import { ShiftsService } from "app/attendance-setup/services/shifts.service";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { AttendancePlanService } from "app/attendance-setup/services/attendance-plan.service";
import { IResponse } from "app/shared/interfaces/responces";

@Component({
	selector: "app-add-plan",
	templateUrl: "./add-plan.component.html",
	styleUrls: ["./add-plan.component.scss"],
})
export class AddPlanComponent extends AppHelpers implements OnInit {
	planId: number = 0;
	constructor(

		public shiftsService: ShiftsService,
		public planService: AttendancePlanService
	) {
		super();
	}

	ngOnInit(): void {
		this.activatedRoute.paramMap.subscribe((params) => {
			this.planId = Number(params.get("id")) ?? 0;
		});
	}

	submit(model: IAttendancePlanRequest) {
		if (model?.plan?.id >= 0) model.plan.id == 0 ? this.create(model) : this.update(model);
	}

	create(model: IAttendancePlanRequest) {
		this.showLoading();
		this.planService
			.create(model)
			.subscribe((res: IResponse<IAttendancePlanRequest>) => {
				if (res?.message?.messageType == MessageTypes.Success) {
					this.addSuccessfullyMsg(res?.message?.messageTypeName);
					// this.planId = res.data.plan.id;
					// this.formCtrls.id.setValue(this.planId);
					// let url = `/attendance-setup/${this.moduleId}/plan/edit/${this.planId}`;
					// this.navigateTo(url);
					// console.log("plan", this.planId);
				} else this.errorList(res?.message?.messages);
				this.hideLoading();
			});
	}

	update(model: IAttendancePlanRequest) {
		this.showLoading();
		this.planService
			.update(model)
			.subscribe((res: IResponse<IAttendancePlanRequest>) => {
				if (res.message.messageType == MessageTypes.Success) {
					this.updateSuccessfullyMsg(res?.message?.messageTypeName);
				} else this.errorList(res?.message?.messages);
				this.hideLoading();
			});
	}
}
