import { Component, OnInit } from "@angular/core";
import { FormGroup } from "@angular/forms";
import { IPenaltyModel } from "app/attendance-setup/models/attendance-penalty-setup";
import { AttendancePenaltySetupService } from "app/attendance-setup/services/attendance-penalty-setup.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CustomValidators } from "ngx-custom-validators";

@Component({
	selector: "app-attendance-penalty-setup",
	templateUrl: "./attendance-penalty-setup.component.html",
	styleUrls: ["./attendance-penalty-setup.component.scss"],
})
export class AttendancePenaltySetupComponent extends AppHelpers implements OnInit {
	form: FormGroup;
	submitted: boolean = false;
	constructor(private penaltySetupServ: AttendancePenaltySetupService) {
		super();
	}

	ngOnInit(): void {
		// if (this.businessId > 0 && this.companyId > 0) this.getObject();
		this.form = this.fb.group({
			id: [0, { nonNallable: true }],
			maxDeductionLimitDays: [null, [CustomValidators.gt(0)]],
		});
	}

	get formCtrls() {
		return this.form.controls;
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.checkNoBusinessOrCompany()) this.getObject();
	}

	getObject() {
		this.showLoading();
		this.penaltySetupServ.getObject().subscribe((res: IResponse<IPenaltyModel>) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.form.patchValue(res.data);
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	submit() {
		if (this.form.valid) {
			const model: IPenaltyModel = {
				id: this.form.value.id,
				maxDeductionLimitDays: this.form.value.maxDeductionLimitDays,
			};
			model.id == 0 ? this.create(model) : this.update(model);
		}
	}

	create(model: IPenaltyModel) {
		this.showLoading();
		this.penaltySetupServ.create(model).subscribe((res: IResponse<IPenaltyModel>) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.addSuccessfullyMsg(res.message.messageTypeName);
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	update(model: IPenaltyModel) {
		this.showLoading();
		this.penaltySetupServ.update(model).subscribe((res: IResponse<IPenaltyModel>) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.updateSuccessfullyMsg(res.message.messageTypeName);
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}
}
