import { Component, Injector, Input, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import { IVacationAgeModel, IVacationAgeRequest } from "app/attendance-setup/models/vacation";
import { VacationAgeService } from "app/attendance-setup/services/vacation/vacation-age.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CustomValidator } from "app/shared/validators/custom.validator";
import { CustomValidators } from "ngx-custom-validators";

@Component({
	selector: "app-vacation-age",
	templateUrl: "./vacation-age.component.html",
	styleUrls: ["./vacation-age.component.scss"],
})
export class VacationAgeComponent extends AppHelpers implements OnInit {
	@Input() vacationId: number;
	dataSource: MatTableDataSource<IVacationAgeModel>;
	form: FormGroup;
	submitted: boolean = false;
	mandatoryColomns: string[] = ["Ser", "id", "From_Age", "To_Age", "balance", "actions"];
	isLoaded: boolean = false;
	optionalColomns: string[] = [];
	constructor(private vacationAgeServ: VacationAgeService, injector: Injector) {
		super();
	}

	ngOnInit(): void {
		// if (this.businessId > 0 && this.companyId > 0 && this.vacationId > 0) {
		// 	this.getVacationAgeList();
		// }

		this.form = this.fb.group(
			{
				id: [0, { nonNullable: true }],
				Vacation_ID: [0, { nonNullable: true }],
				From_Age: [null, [Validators.required, CustomValidators.gt(0)]],
				To_Age: [null, [Validators.required, CustomValidators.gt(0)]],
				balance: [null, [Validators.required, CustomValidators.gt(0)]],
			},
			{
				validators: [CustomValidator.greaterThan("From_Age", "To_Age")],
			}
		);
	}
	get formCtrls() {
		return this.form.controls;
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.dataSource?.data?.length > 0) this.dataSource = new MatTableDataSource(null);
		if (this.checkNoBusinessOrCompany() && this.vacationId > 0) this.getVacationAgeList();
	}

	getVacationAgeList() {
		let params: IPageParams = {
			PageNumber: 1,
			pageSize: 1,
		};
		this.vacationAgeServ
			.getList(params, this.vacationId)
			.subscribe((res: IResponseList<IVacationAgeModel[]>) => {
				if (res.message.messageType == MessageTypes.Success) {
					res.data.map((e) => {
						e.From_Age = e.from_Age;
						e.To_Age = e.to_Age;
					});
					this.dataSource = new MatTableDataSource(res.data);
					this.isLoaded = true;
				} else this.errorList(res.message.messages);
			});
	}

	submit() {
		this.submitted = true;
		if (this.form.valid) {
			const model: IVacationAgeRequest = {
				id: this.form.value.id,
				Vacation_ID: this.vacationId,
				From_Age: this.form.value.From_Age,
				To_Age: this.form.value.To_Age,
				Balance: this.form.value.balance,
			};
			model.id == 0 ? this.create(model) : this.update(model);
		}
	}

	create(model: IVacationAgeRequest) {
		this.showLoading();
		this.vacationAgeServ.create(model).subscribe((res: IResponse<IVacationAgeModel>) => {
			if (res?.message?.messageType == MessageTypes.Success) {
				this.getVacationAgeList();
				this.addSuccessfullyMsg(res?.message?.messageTypeName);
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	update(model: IVacationAgeRequest) {
		this.showLoading();
		this.vacationAgeServ.update(model).subscribe((res: IResponse<IVacationAgeModel>) => {
			if (res?.message?.messageType == MessageTypes.Success) {
				this.clearForm();
				this.getVacationAgeList();
				this.updateSuccessfullyMsg(res?.message?.messageTypeName);
			} else {
				this.errorList(res?.message?.messages);
			}
			this.hideLoading();
		});
	}

	vacationAgeToEdit(vacationAge: IVacationAgeModel) {
		this.form.patchValue(vacationAge);
	}

	onDelete(vacationAge: IVacationAgeModel) {
		let msg = this.msgTranslate(vacationAge.to_Age.toString());
		this.deleateDialog(msg).subscribe((confirm) => {
			if (confirm) {
				this.delete(vacationAge);
			}
		});
	}

	delete(model: IVacationAgeModel) {
		this.showLoading();
		this.vacationAgeServ.Delete(model.id).subscribe((res: IResponse) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
				this.getVacationAgeList();
				if (model.id == this.formCtrls.id.value) this.clearForm();
			} else {
				this.errorList(res?.message?.messages);
			}
		});
	}

	clearForm() {
		this.form.reset({ id: 0 });
		this.submitted = false;
	}
}
