import { Component, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import {
	IOverTimeTypeModel,
	IOverTimeTypeRequest,
} from "app/attendance-setup/models/over-time-type";
import { OverTimeTypeService } from "app/attendance-setup/services/over-time-type.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CustomValidator } from "app/shared/validators/custom.validator";

@Component({
	selector: "app-over-time-type",
	templateUrl: "./over-time-type.component.html",
	styleUrls: ["./over-time-type.component.scss"],
})
export class OverTimeTypeComponent extends AppHelpers implements OnInit {
	form: FormGroup;
	submitted: boolean = false;
	mandatoryColomns: string[] = ["Ser", "id", "name", "ar_Name", "actions"];
	isLoaded: boolean = false;
	optionalColomns: string[] = [];
	dataSource: MatTableDataSource<IOverTimeTypeModel>;

	constructor(private overTimeTypeService: OverTimeTypeService) {
		super();
	}

	ngOnInit(): void {
		// if (this.businessId > 0 && this.companyId > 0)
		//   this.getList(this.page.pageIndex);
		this.form = this.fb.group({
			id: [0, { nonNullable: true }],
			name: [null, [Validators.required, CustomValidator.whiteSpace]],
			ar_Name: [null, [Validators.required, CustomValidator.whiteSpace]],
		});
	}

	get formCtrls() {
		return this.form.controls;
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.checkNoBusinessOrCompany()) this.getList(this.firstPage);
	}

	getList(page: number, pageSize?: number) {
		this.page.pageIndex = page;
		if (pageSize) this.page.pageSize = pageSize;
		let params: IPageParams = {
			PageNumber: page,
			pageSize: this.page.pageSize,
		};
		this.overTimeTypeService
			.getList(params)
			.subscribe((res: IResponseList<IOverTimeTypeModel[]>) => {
				if (res.message.messageType == MessageTypes.Success) {
					this.dataSource = new MatTableDataSource(res.data);
					this.isLoaded = true;
					this.page.length = res.meta.totalItemCount;
				} else this.errorList(res.message.messages);
			});
	}

	submit() {
		this.submitted = true;
		if (this.form.valid) {
			const model: IOverTimeTypeRequest = {
				ID: this.form.value.id,
				Ar_Name: this.form.value.ar_Name,
				Name: this.form.value.name,
			};

			model.ID == 0 ? this.create(model) : this.update(model);
		}
	}

	create(model: IOverTimeTypeRequest) {
		this.showLoading();
		this.overTimeTypeService.create(model).subscribe((res) => {
			if (res?.message?.messageType == MessageTypes.Success) {
				this.clearForm();
				this.getList(this.lastPage, this.page.pageSize);
				this.addSuccessfullyMsg(res?.message?.messageTypeName);
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	update(model: IOverTimeTypeRequest) {
		this.showLoading();
		this.overTimeTypeService.update(model).subscribe((res: IResponse<IOverTimeTypeModel>) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.clearForm();
				this.getList(this.currentPage, this.page.pageSize);
				this.updateSuccessfullyMsg(res?.message?.messageTypeName);
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	attendanceElementToEdit(overTimeType: IOverTimeTypeModel) {
		this.form.patchValue(overTimeType);
	}

	onDelete(overTimeType: IOverTimeTypeModel): void {
		this.deleateDialog(overTimeType.name).subscribe((confirm) => {
			if (confirm) this.delete(overTimeType);
		});
	}

	delete(overTimeType: IOverTimeTypeModel) {
		this.showLoading();
		this.overTimeTypeService.Delete(overTimeType.id).subscribe((res: IResponse) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
				if (this.dataSource.data.length == 1 && this.currentPage > 1) {
					this.getList(this.prevPage, this.page.pageSize);
				} else {
					this.getList(this.currentPage, this.page.pageSize);
				}
				if (overTimeType.id == this.formCtrls.id.value) this.clearForm();
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	clearForm() {
		this.form.reset({ id: 0 });
		this.submitted = false;
	}
}
