import { Component, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { IShiftRequest, IShiftsModel } from "app/attendance-setup/models/shifts";
import { ShiftsService } from "app/attendance-setup/services/shifts.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CustomValidator } from "app/shared/validators/custom.validator";
@Component({
	selector: "app-shifts",
	templateUrl: "./add-shifts.component.html",
	styleUrls: ["./add-shifts.component.scss"],
})
export class ShiftsComponent extends AppHelpers implements OnInit {
	shiftId: number = 0;
	form: FormGroup;
	submitted: boolean = false;
	Shift: IShiftsModel;
	constructor(private shiftsService: ShiftsService) {
		super();
	}

	ngOnInit(): void {
		this.activatedRoute.paramMap.subscribe((params) => {
			this.shiftId = Number(params.get("shiftId")) ?? 0;
			// if (this.CheckNoBusinessOrCompany() && this.companyId > 0)
			//   this.getShiftsById();
		});

		this.form = this.fb.group(
			{
				id: [0, { nonNullable: true }],
				ar_Name: [null, [Validators.required, CustomValidator.whiteSpace]],
				en_Name: [null, [Validators.required, CustomValidator.whiteSpace]],
				is_Just_Attend: [false],
				is_Target: [false],
				hour_Number: [null, [Validators.required, Validators.min(0)]],
				ovt_Target_Factor: [null, Validators.min(0)],
				ovt_Target_Max_Limit: [null, Validators.min(0)],
				delay_Tolerance: [null, Validators.min(0)],
				across_Mid_Night: [false],
				isNight: [false],
				is_DefaultAttend: [false],
				calc_Delay_From: [null],
				calc_Departure_From: [null],
				start_Time_Lower_Limit: [null],
				start_Time_Upper_Limit: [null],
				end_Time_Lower_Limit: [null, { disable: true }],
				end_Time_Upper_Limit: [null],
				start_Time: [null],
				end_Time: [null],
			},
			{
				validator: [
					CustomValidator.timeAfter("end_Time", "start_Time"),
					CustomValidator.timeBefore("start_Time_Lower_Limit", "start_Time"),
					CustomValidator.timeBefore("start_Time_Lower_Limit", "end_Time"),
					CustomValidator.timeAfter("calc_Departure_From", "calc_Delay_From"),
					CustomValidator.timeAfter("calc_Delay_From", "start_Time"),
					CustomValidator.timeBefore("calc_Departure_From", "end_Time"),
					CustomValidator.timeAfter("start_Time_Upper_Limit", "start_Time_Lower_Limit"),
					CustomValidator.timeAfter("end_Time_Upper_Limit", "end_Time_Lower_Limit"),
					CustomValidator.timeAfter("end_Time_Upper_Limit", "end_Time"),
					CustomValidator.timeAfter("start_Time_Upper_Limit", "start_Time"),
					CustomValidator.timeBefore("start_Time_Upper_Limit", "end_Time"),
				],
			}
		);

		this.formCtrls.start_Time.valueChanges.subscribe((val) => {
			this.calcHourNumber();
		});

		this.formCtrls.end_Time.valueChanges.subscribe((val) => {
			this.calcHourNumber();
		});
	}

	get formCtrls() {
		return this.form.controls;
	}

	override onChangeBusinessAndCompanyId(): void {
		// this.Shift = null;
		if (this.checkNoBusinessOrCompany()) this.getShiftsById();
	}

	onchangeJustAttend(value?: boolean) {
		let JUSTAttendDisabled = [
			"hour_Number",
			"ovt_Target_Factor",
			"ovt_Target_Max_Limit",
			"across_Mid_Night",
			"delay_Tolerance",
			"isNight",
			"is_DefaultAttend",
			"end_Time",
			"calc_Delay_From",
			"calc_Departure_From",
			"start_Time",
		];
		JUSTAttendDisabled.forEach((control) => {
			if (value) {
				this.formCtrls.is_Target.setValue(false);
				this.form.get(control).disable();
				this.form.get(control).reset();
			} else this.form.get(control).enable();
		});
	}

	onchangeTarget(value?: boolean) {
		let changeTarget = [
			"start_Time",
			"end_Time",
			"calc_Delay_From",
			"calc_Departure_From",
			"isNight",
			"is_DefaultAttend",
		];
		changeTarget.forEach((control) => {
			if (value) {
				this.formCtrls.is_Just_Attend.setValue(false);
				this.form.get(control).disable();
				this.form.get(control).reset();
				// enable
				this.formCtrls.hour_Number.enable();
				this.formCtrls.ovt_Target_Max_Limit.enable();
				this.formCtrls.ovt_Target_Factor.enable();
				this.formCtrls.across_Mid_Night.enable();
				this.formCtrls.delay_Tolerance.enable();
			} else {
				this.form.get(control).enable();
			}
		});
	}

	calcHourNumber() {
		this.formCtrls?.hour_Number?.disable();
		let start = new Date("01/01/2000 " + this.formCtrls.start_Time.value);
		let end = new Date("01/01/2000 " + this.formCtrls.end_Time.value);
		var start_Hours = start.getHours();
		var end_Hours = end.getHours();
		var start_Minu = start.getMinutes();
		var end_Minu = end.getMinutes();
		let newTime = end_Hours - start_Hours + (end_Minu - start_Minu) / 60;
		if (newTime) this.formCtrls.hour_Number.setValue(newTime);
		else this.formCtrls.hour_Number.setValue(null);
	}

	changeStartTimeUpperLimit() {
		this.formCtrls.end_Time_Lower_Limit.disable();
		let time = new Date("01/01/2000 " + this.formCtrls.start_Time_Upper_Limit.value);
		time.setSeconds(time.getSeconds() + 1);
		var newTime = time.toTimeString().split(" ")[0];
		this.formCtrls.end_Time_Lower_Limit.setValue(newTime);
		this.formCtrls.end_Time_Lower_Limit.disable();
	}

	submit() {
		this.submitted = true;
		if (this.form.valid) {
			const model: IShiftRequest = {
				id: this.form.value.id,
				ar_Name: this.form.value.ar_Name,
				en_Name: this.form.value.en_Name,
				start_Time: this.form.value.start_Time,
				end_Time: this.form.value.end_Time,
				across_Mid_Night: this.form.value.across_Mid_Night,
				is_Target: this.form.value.is_Target,
				ovt_Target_Factor: this.form.value.ovt_Target_Factor,
				delay_Tolerance: this.form.value.delay_Tolerance,
				calc_Delay_From: this.form.value.calc_Delay_From,
				calc_Departure_From: this.form.value.calc_Departure_From,
				is_Just_Attend: this.form.value.is_Just_Attend,
				is_DefaultAttend: this.form.value.is_DefaultAttend,
				hour_Number: this.form.value.hour_Number,
				isNight: this.form.value.isNight,
				ovt_Target_Max_Limit: this.form.value.ovt_Target_Max_Limit,
				start_Time_Lower_Limit: this.form.value.start_Time_Lower_Limit,
				start_Time_Upper_Limit: this.form.value.start_Time_Upper_Limit,
				end_Time_Lower_Limit: this.form.value.end_Time_Lower_Limit,
				end_Time_Upper_Limit: this.form.value.end_Time_Upper_Limit,
			};
			model.id == 0 ? this.create(model) : this.update(model);
		}
	}

	create(model: IShiftRequest) {
		this.showLoading();
		this.shiftsService.create(model).subscribe((res: any) => {
			if (res?.message?.messageType == MessageTypes.Success) {
				this.addSuccessfullyMsg(res?.message?.messageTypeName);
				this.shiftId = res.data.id;
			} else this.errorList(res?.message?.messages);
			this.hideLoading();
		});
	}

	update(model: IShiftRequest) {
		this.showLoading();
		this.shiftsService.update(model).subscribe((res: IResponse<IShiftsModel>) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.shiftId = res.data.id;
				this.updateSuccessfullyMsg(res?.message?.messageTypeName);
			} else this.errorList(res?.message?.messages);
			this.hideLoading();
		});
	}

	getShiftsById() {
		this.shiftsService.getById(this.shiftId).subscribe((res: IResponse<IShiftsModel[]>) => {
			if ((res.message.messageType = MessageTypes.Success)) {
				this.form.patchValue(res.data[0]);
			}
		});
	}
}
