import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import { IAttendancePeriod } from "../models/attendance-period";
import { IResponse } from "app/shared/interfaces/responces";


@Injectable({
  providedIn: "root",
})
export class AttendancePeriodService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IAttendancePeriod) {
    return this.post<IResponse<IAttendancePeriod>>(
      "AttendanceSetup/AttendancePeriodSetup/Create",
      data
    );
  }

  update(data: IAttendancePeriod) {
    return this.post<IResponse<IAttendancePeriod>>(
      "AttendanceSetup/AttendancePeriodSetup/Edit",
      data
    );
  }

  getObject() {
    return this.get<IResponse<IAttendancePeriod>>(
      "AttendanceSetup/AttendancePeriodSetup/GetObject",
  
    );
  }
}
