import { Component, Injector, OnInit } from "@angular/core";
import { IAttendancePlanRequest } from "app/attendance-setup/models/plan";
import { AttendancePlanService } from "app/attendance-setup/services/attendance-plan.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
	selector: "app-shrink",
	templateUrl: "./shrink.component.html",
	styleUrls: ["./shrink.component.scss"],
})
export class ShrinkComponent extends AppHelpers implements OnInit {
	constructor(private planService: AttendancePlanService) {
		super();
	}

	ngOnInit(): void {}

	submit(model: IAttendancePlanRequest) {
		delete model.plan.description;
		delete model.plan.group_Days;
		delete model.Att_Plan_ShiftList;

		if (model.plan.id) this.create(model);
	}

	create(model) {
		this.showLoading();
		this.planService.createShrink(model).subscribe((res: IResponse<IAttendancePlanRequest>) => {
			if (res?.message?.messageType == MessageTypes.Success) {
				this.addSuccessfullyMsg(res?.message?.messageTypeName);
			} else this.errorList(res?.message?.messages);
			this.hideLoading();
		});
	}
}
