import { Component, Input, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import {
	IVacationInsuranceModel,
	IVacationInsuranceRequest,
} from "app/attendance-setup/models/vacation";
import { VacationInsuranceService } from "app/attendance-setup/services/vacation/vacation-insurance.service";
import { RadioButtonModel } from "app/shared/components/dynamic-form-field/radio-button/radio-button.component";
import { EInsuranceSide } from "app/shared/enums/insuranceSide.enum";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { HelpersFunctions } from "app/shared/resources/helpers-functions";
import { CustomValidator } from "app/shared/validators/custom.validator";
import { CustomValidators } from "ngx-custom-validators";

@Component({
	selector: "app-vacation-insurance",
	templateUrl: "./vacation-insurance.component.html",
	styleUrls: ["./vacation-insurance.component.scss"],
})
export class VacationInsuranceComponent extends AppHelpers implements OnInit {
	@Input() vacationId: number;
	dataSource: MatTableDataSource<IVacationInsuranceModel>;
	form: FormGroup;
	submitted: boolean = false;
	mandatoryColomns: string[] = [
		"Ser",
		"id",
		"from_Year",
		"to_Year",
		"balance",
		"inside_CompanyString",
		"actions",
	];
	optionalColomns: string[] = ["ignore_Insurance_Date"];
	isLoaded: boolean = false;
	constructor(private vacationInsServ: VacationInsuranceService) {
		super();
	}

	ngOnInit(): void {
		// if (this.businessId > 0 && this.companyId > 0 && this.vacationId > 0)
		// 	this.getVacationInsuranceList();

		this.form = this.fb.group(
			{
				id: [0, { nonNallable: true }],
				Vacation_ID: [0, { nonNullable: true }],
				from_Year: [null, [Validators.required, CustomValidators.gt(0)]],
				to_Year: [null, [Validators.required, CustomValidators.gt(0)]],
				balance: [null, [Validators.required, CustomValidators.gt(0)]],
				inside_Company: [null, [Validators.required]],
				ignore_Insurance_Date: [false],
			},
			{
				validators: [CustomValidator.greaterThan("from_Year", "to_Year")],
			}
		);
	}
	get formCtrls() {
		return this.form.controls;
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.dataSource?.data?.length > 0) this.dataSource = new MatTableDataSource(null);
		if (this.checkNoBusinessOrCompany() && this.vacationId > 0) this.getVacationInsuranceList();
	}

	getVacationInsuranceList() {
		let params: IPageParams = {
			PageNumber: 1,
			pageSize: 1,
		};
		this.vacationInsServ
			.getList(params, this.vacationId)
			.subscribe((res: IResponse<IVacationInsuranceModel[]>) => {
				if (res.message.messageType == MessageTypes.Success) {
					res.data.map((e) => {
						e.inside_CompanyString = this.translate(
							EInsuranceSide.getValue(e.inside_Company)
						);
					});
					this.dataSource = new MatTableDataSource(res.data);
					this.isLoaded = true;
				} else this.errorList(res.message.messages);
			});
	}

	submit() {
		this.submitted = true;
		if (this.form.valid) {
			const model: IVacationInsuranceRequest = {
				ID: this.form.value.id,
				Vacation_ID: this.vacationId,
				From_Year: this.form.value.from_Year,
				To_Year: this.form.value.to_Year,
				Balance: this.form.value.balance,
				inside_Company: this.form.value.inside_Company,
				ignore_Insurance_Date: this.form.value.ignore_Insurance_Date,
			};
			model.ID == 0 ? this.create(model) : this.update(model);
		}
	}

	create(model: IVacationInsuranceRequest) {
		this.showLoading();
		this.vacationInsServ.create(model).subscribe((res: IResponse<IVacationInsuranceModel>) => {
			if (res?.message?.messageType == MessageTypes.Success) {
				this.getVacationInsuranceList();
				this.addSuccessfullyMsg(res?.message?.messageTypeName);
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	update(model: IVacationInsuranceRequest) {
		this.showLoading();
		this.vacationInsServ.update(model).subscribe((res: IResponse<IVacationInsuranceModel>) => {
			if (res?.message?.messageType == MessageTypes.Success) {
				this.clearForm();
				this.getVacationInsuranceList();
				this.updateSuccessfullyMsg(res?.message?.messageTypeName);
			} else {
				this.errorList(res?.message?.messages);
			}
			this.hideLoading();
		});
	}

	onDelete(vacationInsurance: IVacationInsuranceModel) {
		let msg = this.msgTranslate(vacationInsurance.to_Year.toString());
		this.deleateDialog(msg).subscribe((confirm) => {
			if (confirm) {
				this.delete(vacationInsurance);
			}
		});
	}

	vacationInsuranceEdit(vacationInsurance: IVacationInsuranceModel) {
		this.form.patchValue(vacationInsurance);
	}

	delete(model: IVacationInsuranceModel) {
		this.showLoading();
		this.vacationInsServ.Delete(model.id).subscribe((res: IResponse) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
				this.getVacationInsuranceList();
				if (model.id == this.formCtrls.id.value) this.clearForm();
			} else {
				this.errorList(res?.message?.messages);
			}
		});
	}

	insuranceSide = EInsuranceSide;
	insuranceSideRadio: RadioButtonModel[] = [
		{
			value: EInsuranceSide.outside_Company,
			label: "outside_Company",
		},
		{
			value: EInsuranceSide.inside_Company,
			label: "inside_Company",
		},
	];

	clearForm() {
		this.form.reset({ id: 0 });
		this.submitted = false;
	}
}
