import { Component, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import { ILocationModel, ILocationRequest } from "app/attendance-setup/models/location";
import { LocationService } from "app/attendance-setup/services/location.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CustomValidator } from "app/shared/validators/custom.validator";

@Component({
	selector: "app-location",
	templateUrl: "./location.component.html",
	styleUrls: ["./location.component.scss"],
})
export class LocationComponent extends AppHelpers implements OnInit {
	dataSource: MatTableDataSource<ILocationModel>;
	form: FormGroup;
	submitted: boolean = false;
	mandatoryColomns: string[] = ["Ser", "id", "en_Name", "ar_Name", "actions"];
	isLoaded: boolean = false;
	optionalColomns: string[] = [];
	constructor(private locationService: LocationService) {
		super();
	}

	ngOnInit(): void {
		// if (this.businessId > 0 && this.companyId > 0)
		//   this.getList(this.currentPage);

		this.form = this.fb.group({
			id: [0, { nonNullable: true }],
			ar_Name: [null, [Validators.required, CustomValidator.whiteSpace]],
			en_Name: [null, [Validators.required, CustomValidator.whiteSpace]],
		});
	}

	get formCtrls() {
		return this.form.controls;
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.dataSource?.data?.length > 0) this.dataSource = new MatTableDataSource(null);
		if (this.checkNoBusinessOrCompany()) this.getList(this.firstPage);
	}

	getList(page: number, pageSize?: number) {
		this.page.pageIndex = page;
		if (pageSize) this.page.pageSize = pageSize;
		let params: IPageParams = {
			PageNumber: page,
			pageSize: this.page.pageSize,
		};
		this.locationService.getList(params).subscribe((res: IResponseList<ILocationModel[]>) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.dataSource = new MatTableDataSource(res.data);
				this.isLoaded = true;
				this.page.length = res.meta.totalItemCount;
			} else this.errorList(res.message.messages);
		});
	}

	submit() {
		this.submitted = true;
		if (this.form.valid) {
			const model: ILocationRequest = {
				ID: this.form.value.id,
				Ar_Name: this.form.value.ar_Name,
				En_Name: this.form.value.en_Name,
			};
			model.ID == 0 ? this.create(model) : this.update(model);
		}
	}

	create(model: ILocationRequest) {
		this.showLoading();
		this.locationService.create(model).subscribe((res: IResponse<ILocationModel>) => {
			if (res?.message?.messageType == MessageTypes.Success) {
				this.clearForm();
				this.getList(this.lastPage, this.page.pageSize);
				this.addSuccessfullyMsg(res?.message?.messageTypeName);
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	update(model: ILocationRequest) {
		this.showLoading();
		this.locationService.update(model).subscribe((res: IResponse<ILocationModel>) => {
			if (res?.message?.messageType == MessageTypes.Success) {
				this.clearForm();
				this.getList(this.currentPage, this.page.pageSize);
				this.updateSuccessfullyMsg(res?.message?.messageTypeName);
			} else {
				this.errorList(res?.message?.messages);
			}
			this.hideLoading();
		});
	}

	locationToEdit(location: ILocationModel) {
		this.form.patchValue(location);
	}

	onDelete(location: ILocationModel) {
		let msg = this.msgTranslate(location.ar_Name, location.en_Name);
		this.deleateDialog(msg).subscribe((confirm) => {
			if (confirm) {
				this.delete(location);
			}
		});
	}

	delete(model: ILocationModel) {
		this.showLoading();
		this.locationService.Delete(model.id).subscribe((res: IResponse) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
				if (this.dataSource.data.length == 1 && this.currentPage > 1) {
					this.getList(this.prevPage, this.page.pageSize);
				} else {
					this.getList(this.currentPage, this.page.pageSize);
				}
				if (model.id == this.formCtrls.id.value) this.clearForm();
			} else {
				this.errorList(res?.message?.messages);
			}
		});
	}

	clearForm() {
		this.form.reset({ id: 0 });
		this.submitted = false;
	}
}
