export enum EInsuranceSide {
	outside_Company = 1,
	inside_Company = 2,
}
export namespace EInsuranceSide {
	export function getValue(gender: EInsuranceSide | number): string {
		switch (gender) {
			case EInsuranceSide.outside_Company:
				return "outside_Company";
			case EInsuranceSide.inside_Company:
				return "inside_Company";
			default:
				return "";
		}
	}
}
