import { Component, OnInit } from "@angular/core";
import { MatTableDataSource } from "@angular/material/table";
import { IVacationModel, IVacationResponse } from "app/attendance-setup/models/vacation";
import { VacationService } from "app/attendance-setup/services/vacation/vacation.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
	selector: "app-vacation-index",
	templateUrl: "./vacation-index.component.html",
	styleUrls: ["./vacation-index.component.scss"],
})
export class VacationIndexComponent extends AppHelpers implements OnInit {
	dataSource: MatTableDataSource<IVacationModel>;
	isLoaded: boolean = false;
	mandatoryColomns: string[] = [
		"Ser",
		"id",
		"en_Name",
		"allow_Post",
		"max_Post_Days",
		"offecial_Vaction_Included",
		"actions",
	];
	optionalColomns: string[] = [
		"display_In_Result",
		"defaultBalance",
		"day_Off_Included",
		"isRenew",
		"renew_Day",
		"renew_Month",
		"renew_Period",
		"number_of_Earning",
		"isDefault",
		"is_Paid",
		"canBeRequestedToday",
		"maxRelatedBal",
		"deductible_Value",
		"relatedVacationId",
		"relatedVacationPriority",
		"hasAttashment",
		"calculate_Balance",
		"calculate_Balance_By_Contract",
		"number_Days_month",
		"reportOrder",
		"gender",
	];
	constructor(private vacationService: VacationService) {
		super();
	}

	ngOnInit(): void {
		// if (this.CheckNoBusinessOrCompany()) {
		//   this.getList(this.currentPage);
		// }
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.checkNoBusinessOrCompany())
			this.getList(this.pageFromUrl > 0 ? this.pageFromUrl : this.firstPage);
	}

	getList(page: number, pageSize?: number) {
		this.page.pageIndex = page;
		if (pageSize) {
			this.page.pageSize = pageSize;
		}
		this.removePageParams();
		const params: IPageParams = {
			PageNumber: page,
			pageSize: this.page.pageSize,
		};
		this.vacationService.getList(params).subscribe((res: IResponseList<IVacationResponse[]>) => {
			if (res.message.messageType === MessageTypes.Success) {
				let list: IVacationModel[] = [];
				res.data.map((e) => {
					list.push(e.att_Vacation);
				});
				this.dataSource = new MatTableDataSource(list);
				this.isLoaded = true;
				this.page.length = res.meta.totalItemCount;
			} else {
				this.errorList(res.message.messages);
			}
		});
	}

	// navigateToEdit(vacation: IVacationModel) {
	//   this.router.navigate(
	//     [`attendance-setup/${this.moduleId}/edit/${vacation.att_Vacation.id}`],
	//     {
	//       queryParams: {
	//         parentView: this.getViewName,
	//         ...this.pageParams,
	//       },
	//     }
	//   );
	// }

	onDelete(vacation: any): void {
		let msg = this.msgTranslate(vacation.ar_Name, vacation.en_Name);
		this.deleateDialog(msg).subscribe((confirm) => {
			if (confirm) this.delete(vacation);
		});
	}

	delete(vacation: IVacationModel) {
		this.showLoading();
		this.vacationService.Delete(vacation.id).subscribe((res: IResponse) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.deleteSuccessfullyMsg(res.message.messageTypeName);
				if (this.dataSource.data.length == 1 && this.page.pageIndex > 1) {
					this.getList(this.prevPage, this.page.pageSize);
				} else this.getList(this.currentPage, this.page.pageSize);
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}
}
