import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import { IShiftRequest, IShiftsModel } from "../models/shifts";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { IPageParams } from "app/shared/interfaces/params";

@Injectable({
	providedIn: "root",
})
export class ShiftsService extends AppClientService {
	constructor() {
		super();
	}

	create(body: IShiftRequest) {
		return this.post<IResponse<IShiftsModel>>("AttendanceSetup/Shift/Create", body);
	}

	update(data: IShiftRequest) {
		return this.post<IResponse<IShiftsModel>>("AttendanceSetup/Shift/Edit", data);
	}

	getList(params: IPageParams) {
		return this.get<IResponseList<IShiftsModel[]>>("AttendanceSetup/Shift/GetList", {
			params: params,
		});
	}

	getById(id: number) {
		return this.get<IResponse<IShiftsModel[]>>("AttendanceSetup/Shift/GetAll", {
			params: {
				$filter: `id eq ${id}`,
			},
		});
	}

	getAll() {
		return this.get<IResponse<IShiftsModel[]>>("AttendanceSetup/Shift/GetAll", {});
	}

	Delete(id: number) {
		return this.delete<IResponse>("AttendanceSetup/Shift/Delete", {
			params: { Id: id },
		});
	}

	//   createExtendWithChange(data: IGradeRequest, params) {
	//     return this.post<IResponse<IGradeModel>>("Grade/Create", data, {
	//       params: params,
	//     });

	// }
}
