import { Injectable } from "@angular/core";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import {
  IOverTimeTypeRequest,
  IOverTimeTypeModel,
} from "../models/over-time-type";
import { IPageParams } from "app/shared/interfaces/params";

@Injectable({
  providedIn: "root",
})
export class OverTimeTypeService extends AppClientService {
  constructor() {
    super();
  }

  create(body: IOverTimeTypeRequest) {
    return this.post<IResponse<IOverTimeTypeModel>>(
      "AttendanceSetup/OverTimeType/Create",
      body
    );
  }

  update(data: IOverTimeTypeRequest) {
    return this.post<IResponse<IOverTimeTypeModel>>(
      "AttendanceSetup/OverTimeType/Edit",
      data
    );
  }

  getList(params: IPageParams) {
    return this.get<IResponseList<IOverTimeTypeModel[]>>(
      "AttendanceSetup/OverTimeType/GetList",
      { params: params }
    );
  }

  getAll() {
    this.get<IResponse<IOverTimeTypeModel[]>>(
      "AttendanceSetup/OverTimeType/GetAll"
    );
  }

  Delete(id: number) {
    return this.delete<IResponse>("AttendanceSetup/OverTimeType/Delete", {
      params: { Id: id },
    });
  }
}
