import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { OfficialHolidaysComponent } from "./components/official-holidays/official-holidays.component";
import { AttendanceDeviceComponent } from "./components/attandance-device/attendance-device.component";
import { LocationComponent } from "./components/location/location.component";
import { AddPermissionComponent } from "./components/permissions/add-permission/add-permission.component";
import { PermissionIndexComponent } from "./components/permissions/permission-index/permission-index.component";
import { VacationTypeComponent } from "./components/vacation/vacation-type/vacation-type.component";
import { VacationIndexComponent } from "./components/vacation/vacation-index/vacation-index.component";
import { AttendanceElementComponent } from "./components/attendance-element/attendance-element.component";
import { OverTimeTypeComponent } from "./components/over-time-type/over-time-type.component";
import { ShiftsComponent } from "./components/shifts/add-shifts/add-shifts.component";
import { ShiftsIndexComponent } from "./components/shifts/shifts-index/shifts-index.component";
import { PlanIndexComponent } from "./components/plan/plan-index/plan-index.component";
import { AddPlanComponent } from "./components/plan/add-plan/add-plan.component";
import { AttendancePeriodComponent } from "./components/attendance-period/attendance-period.component";
import { AttendancePenaltySetupComponent } from "./components/attendance-penalty-setup/attendance-penalty-setup.component";
import { ShrinkComponent } from "./components/plan/shrink/shrink.component";
import { ExtendWithoutChangeComponent } from "./components/plan/extend-without-change/extend-without-change.component";
import { ExtendWithChangeComponent } from "./components/plan/extend-with-change/extend-with-change.component";
import { PlanDetailsComponent } from "./components/plan/plan-details/plan-details.component";
import { pathMatch } from "app/shared/enums/route-path-match.enum";
import { ViewsManager } from "app/shared/resources/views-manager";

const routes: Routes = [
  {
    path: "attendance-devices",
    component: AttendanceDeviceComponent,
    data: { view: ViewsManager.attendance_devices },
  },
  {
    path: "official-holidays",
    component: OfficialHolidaysComponent,
    data: { view: ViewsManager.official_holidays },
  },
  {
    path: "holidays-calendar/:holidayid",
    component: OfficialHolidaysComponent,
    data: { view: ViewsManager.holidays_calendar },
  },
  {
    path: "location",
    component: LocationComponent,
    data: { view: ViewsManager.location },
  },
  {
    path: "attendance-element",
    component: AttendanceElementComponent,
    data: { view: ViewsManager.attendance_element },
  },
  {
    path: "over-time-type",
    component: OverTimeTypeComponent,
    data: { view: ViewsManager.over_time_type },
  },
  {
    path: "permissions",
    children: [
      {
        path: "",
        redirectTo: "index",
        pathMatch: pathMatch.full,
      },
      {
        path: "index",
        component: PermissionIndexComponent,
        data: { view: ViewsManager.permission_index },
      },
      {
        path: "add",
        component: AddPermissionComponent,
        data: { view: ViewsManager.add_permission },
      },
      {
        path: "edit/:permissionId",
        component: AddPermissionComponent,
        data: { view: ViewsManager.edit_permission },
      },
    ],
  },
  {
    path: "vacations",
    children: [
      {
        path:"",
        redirectTo: "index",
        pathMatch: pathMatch.full,
      },
      {
        path: "index",
        component: VacationIndexComponent,
        data: { view: ViewsManager.vacation_index },
      },

      {
        path: "add",
        component: VacationTypeComponent,
        data: { view: ViewsManager.add_vacation },
      },
      {
        path: "edit/:vacationId",
        component: VacationTypeComponent,
        data: { view: ViewsManager.add_vacation },
      },
    ],
  },
  {
    path: "shifts",
    children: [
      {
        path:"",
        redirectTo: "index",
        pathMatch: pathMatch.full,
      },
      {
        path: "index",
        component: ShiftsIndexComponent,
        data: { view: ViewsManager.shifts_index },
      },
      {
        path: "add",
        component: ShiftsComponent,
        data: { view: ViewsManager.add_shift },
      },

      {
        path: "edit/:shiftId",
        component: ShiftsComponent,
        data: { view: ViewsManager.add_shift },
      },
    ],
  },
  {
    path: "plan",
    children: [
      {
        path:"",
        redirectTo: "index",
        pathMatch: pathMatch.full,
      },
      {
        path: "index", // plan/index
        component: PlanIndexComponent,
        data: { view: ViewsManager.plan_index },
      },
      {
        path: "add", // plan/add
        component: AddPlanComponent,
        data: { view: ViewsManager.add_plan },
      },
      {
        path: "edit/:planId",
        component: AddPlanComponent,
        data: { view: ViewsManager.add_plan },
      },

      {
        path: "shrink/:planId",
        component: ShrinkComponent,
        data: { view: ViewsManager.shrink },
      },

      {
        path: "extend-without-change/:planId",
        component: ExtendWithoutChangeComponent,
        data: { view: ViewsManager.extend_without_change },
      },
      {
        path: "extend-with-change/:planId",
        component: ExtendWithChangeComponent,
        data: { view: ViewsManager.extend_with_change },
      },
      {
        path: "details/:planId",
        component: PlanDetailsComponent,
        data: { view: ViewsManager.plan_details },
      },
    ],
  },
  {
    path: "attendance-period",
    component: AttendancePeriodComponent,
    data: { view: ViewsManager.attendance_period },
  },
  {
    path: "attendance-penalty-setup",
    component: AttendancePenaltySetupComponent,
    data: { view: ViewsManager.attendance_penalty_setup },
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class AttendanceSetupRoutingModule {}
