import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import {
  IAttendanceElementModel,
  IAttendanceElementRequest,
  IAttendanceElementData,
} from "../models/attendance-element";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { db } from "app/shared/resources/database-tabels";
import { IPageParams } from "app/shared/interfaces/params";

@Injectable({
  providedIn: "root",
})
export class AttendanceElementService extends AppClientService {
  constructor() {
    super();
  }

  create(body: IAttendanceElementRequest) {
    return this.post<IResponse<IAttendanceElementData>>(
      "AttendanceSetup/Element/Create",
      body
    );
  }

  update(data: IAttendanceElementRequest) {
    return this.post<IResponse<IAttendanceElementData>>(
      "AttendanceSetup/Element/Edit",
      data
    );
  }

  getList(params: IPageParams) {
    return this.get<IResponseList<IAttendanceElementModel[]>>(
      "AttendanceSetup/Element/GetList",
      {
        params: {
          ...params,
          $expand: `${db.Att_Attendance_Element},${db.PAY_Variable}`,
        },
      }
    );
  }

  getAll() {
    this.get<IResponse<IAttendanceElementModel[]>>(
      "AttendanceSetup/Element/GetAll"
    );
  }

  Delete(id: number) {
    return this.delete<IResponse>("AttendanceSetup/Element/Delete", {
      params: { Id: id },
    });
  }
}
