import { Injectable } from "@angular/core";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import { IVacationAgeModel, IVacationAgeRequest } from "../../models/vacation";
import { IPageParams } from "app/shared/interfaces/params";

@Injectable({
  providedIn: "root",
})
export class VacationAgeService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IVacationAgeRequest) {
    return this.post<IResponse<IVacationAgeModel>>(
      "AttendanceSetup/VacationTypes/AddVacationAgeBased",
      data
    );
  }

  update(data: IVacationAgeRequest) {
    return this.post<IResponse<IVacationAgeModel>>(
      "AttendanceSetup/VacationTypes/EditVacationAgeBased",
      data
    );
  }

  getList(params: IPageParams, vacationId: number) {
    return this.get<IResponse<IVacationAgeModel[]>>(
      "AttendanceSetup/VacationTypes/GetVacationAgeBasedList",
      {
        params: { ...params, $filter: `vacation_ID eq ${vacationId}` },
      }
    );
  }

  Delete(id: number) {
    return this.delete<IResponse>(
      "AttendanceSetup/VacationTypes/DeleteVacationAgeBased",
      {
        params: { Id: id },
      }
    );
  }
}
