import { Component, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import {
	IAttendanceElementModel,
	IAttendanceElementRequest,
	IAttendanceElementData,
} from "app/attendance-setup/models/attendance-element";
import { AttendanceElementService } from "app/attendance-setup/services/attendance-element.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CustomValidator } from "app/shared/validators/custom.validator";

@Component({
	selector: "app-attendance-element",
	templateUrl: "./attendance-element.component.html",
	styleUrls: ["./attendance-element.component.scss"],
})
export class AttendanceElementComponent extends AppHelpers implements OnInit {
	form: FormGroup;
	submitted: boolean = false;
	mandatoryColomns: string[] = ["Ser", "id", "arName", "name", "code", "actions"];
	optionalColomns: string[] = ["display_In_Result"];
	dataSource: MatTableDataSource<IAttendanceElementModel>;
	isLoaded: boolean = false;
	constructor(private attendanceElementService: AttendanceElementService) {
		super();
	}

	ngOnInit(): void {
		// if (this.businessId > 0 && this.companyId > 0)
		//   this.getList(this.page.pageIndex);
		this.form = this.fb.group({
			id: [0, { nonNullable: true }],
			arName: [null, [Validators.required, CustomValidator.whiteSpace]],
			name: [null, [Validators.required, CustomValidator.whiteSpace]],
			code: [null, [Validators.required]],
			display_In_Result: [false],
		});
	}

	get formCtrls() {
		return this.form.controls;
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.checkNoBusinessOrCompany()) this.getList(this.firstPage);
	}

	getList(page: number, pageSize?: number) {
		this.page.pageIndex = page;
		if (pageSize) this.page.pageSize = pageSize;
		let params: IPageParams = {
			PageNumber: page,
			pageSize: this.page.pageSize,
		};
		this.attendanceElementService
			.getList(params)
			.subscribe((res: IResponseList<IAttendanceElementModel[]>) => {
				if (res.message.messageType == MessageTypes.Success) {
					res.data.map((item: IAttendanceElementModel) => {
						item.arName = item.att_Attendance_Element.ar_Name;
						item.name = item.att_Attendance_Element.en_Name;
						item.code = item.att_Attendance_Element.code;
						item.id = item.att_Attendance_Element.id;
						item.display_In_Result = item.paY_Variable.display_In_Result;
					});
					this.dataSource = new MatTableDataSource(res.data);
					this.isLoaded = true;
					this.page.length = res.meta.totalItemCount;
				} else this.errorList(res.message.messages);
			});
	}

	submit() {
		this.submitted = true;
		if (this.form.valid) {
			const model: IAttendanceElementRequest = {
				element: {
					id: this.form.value.id,
					en_Name: this.form.value.name,
					ar_Name: this.form.value.arName,
					code: this.form.value.code,
				},
				display_In_Result: this.form.value.display_In_Result,
			};

			model.element.id == 0 ? this.create(model) : this.update(model);
		}
	}
	create(model: IAttendanceElementRequest) {
		this.showLoading();
		this.attendanceElementService
			.create(model)
			.subscribe((res: IResponse<IAttendanceElementData>) => {
				if (res?.message?.messageType == MessageTypes.Success) {
					this.clearForm();
					this.getList(this.lastPage, this.page.pageSize);
					this.addSuccessfullyMsg(res?.message?.messageTypeName);
				} else this.errorList(res.message.messages);
				this.hideLoading();
			});
	}

	update(model: IAttendanceElementRequest) {
		this.showLoading();
		this.attendanceElementService
			.update(model)
			.subscribe((res: IResponse<IAttendanceElementData>) => {
				if (res.message.messageType == MessageTypes.Success) {
					this.clearForm();
					this.getList(this.currentPage, this.page.pageSize);
					this.updateSuccessfullyMsg(res?.message?.messageTypeName);
				} else this.errorList(res.message.messages);
				this.hideLoading();
			});
	}

	attendanceElementToEdit(attendanceElement: IAttendanceElementModel) {
		// this.formCtrls.id.setValue(attendanceElement.paY_Variable.id);
		this.form.patchValue(attendanceElement);
	}

	onDelete(element: IAttendanceElementModel): void {
		let msg = this.msgTranslate(element.arName, element.name);
		this.deleateDialog(msg).subscribe((confirm) => {
			if (confirm) this.delete(element);
		});
	}

	delete(element: IAttendanceElementModel) {
		this.showLoading();
		this.attendanceElementService.Delete(element.id).subscribe((res: IResponse) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.deleteSuccessfullyMsg(res?.message?.messageTypeName);

				if (this.dataSource.data.length == 1 && this.currentPage > 1) {
					this.getList(this.prevPage, this.page.pageSize);
				} else {
					this.getList(this.currentPage, this.page.pageSize);
				}
				if (element.attendance_Element_ID == this.formCtrls.id.value) this.clearForm();
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	clearForm() {
		this.form.reset({ id: 0, display_In_Result: false });
		this.submitted = false;
	}
}
