import { Component, OnInit } from "@angular/core";
import { MatTableDataSource } from "@angular/material/table";
import { IShiftsModel } from "app/attendance-setup/models/shifts";
import { ShiftsService } from "app/attendance-setup/services/shifts.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { HelpersFunctions } from "app/shared/resources/helpers-functions";

@Component({
	selector: "app-shifts-index",
	templateUrl: "./shifts-index.component.html",
	styleUrls: ["./shifts-index.component.scss"],
})
export class ShiftsIndexComponent extends AppHelpers implements OnInit {
	submitted: boolean = false;
	mandatoryColomns: string[] = [
		"Ser",
		"id",
		"ar_Name",
		"en_Name",
		"start_Time",
		"end_Time",
		"isNight",
		"is_Just_Attend",
		"actions",
	];
	optionalColomns: string[] = [
		"end_Time_Lower_Limit",
		"end_Time_Upper_Limit",
		"calc_Delay_From",
		"calc_Departure_From",
		"is_Target",
		"ovt_Target_Factor",
		"ovt_Target_Max_Limit",
		"start_Time_Lower_Limit",
		"start_Time_Upper_Limit",
		"delay_Tolerance",
		"hour_Number",
		"is_DefaultAttend",
		"shift_Type_ID",
	];
	dataSource: MatTableDataSource<IShiftsModel>;
	isLoaded: boolean = false;
	constructor(private shiftsService: ShiftsService) {
		super();
	}

	ngOnInit(): void {
		// if (this.CheckNoBusinessOrCompany()) this.getList(this.currentPage);
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.dataSource?.data?.length > 0) this.dataSource = new MatTableDataSource(null);
		if (this.checkNoBusinessOrCompany())
			this.getList(this.pageFromUrl > 0 ? this.pageFromUrl : this.firstPage);
	}

	getList(page: number, pageSize?: number) {
		this.page.pageIndex = page;
		if (pageSize) this.page.pageSize = pageSize;
		this.removePageParams();
		let params: IPageParams = {
			PageNumber: page,
			pageSize: this.page.pageSize,
		};
		this.shiftsService.getList(params).subscribe((res: IResponseList<IShiftsModel[]>) => {
			if (res.message.messageType == MessageTypes.Success) {
				if (res.data.length == 0 && page > 1) {
					this.getList(this.prevPage);
					return;
				}
				res.data.map((e) => {
					e.start_Time = HelpersFunctions.mapTime(e.start_Time);
					e.end_Time = HelpersFunctions.mapTime(e.end_Time);
					e.start_Time_Lower_Limit = HelpersFunctions.mapTime(e.start_Time_Lower_Limit);
					e.start_Time_Upper_Limit = HelpersFunctions.mapTime(e.start_Time_Upper_Limit);
					e.end_Time_Lower_Limit = HelpersFunctions.mapTime(e.end_Time_Lower_Limit);
					e.end_Time_Upper_Limit = HelpersFunctions.mapTime(e.end_Time_Upper_Limit);
					e.calc_Departure_From = HelpersFunctions.mapTime(e.calc_Departure_From);
					e.calc_Delay_From = HelpersFunctions.mapTime(e.calc_Delay_From);
				});
				this.dataSource = new MatTableDataSource(res.data);
				this.isLoaded = true;
				this.page.length = res.meta.totalItemCount;
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	// toEditShifts(shift: IShiftsModel) {
	//   this.router.navigate([`attendance-setup/${this.moduleId}/edit-shift/${shifts.id}`], {
	//     queryParams: {
	//       parentView: this.getViewName,
	//       ...this.pageParams,
	//     },
	//   });
	//   }

	onDelete(shifts: IShiftsModel): void {
		let msg = this.msgTranslate(shifts.ar_Name, shifts.en_Name);
		this.deleateDialog(msg).subscribe((confirm) => {
			if (confirm) this.delete(shifts);
		});
	}

	delete(shifts: IShiftsModel) {
		this.showLoading();
		this.shiftsService.Delete(shifts.id).subscribe((res: IResponse) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
				if (this.dataSource.data.length == 1 && this.currentPage > 1)
					this.getList(this.prevPage, this.page.pageSize);
				else this.getList(this.currentPage, this.page.pageSize);
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}
}
