import { Component, OnInit } from "@angular/core";
import { MatTableDataSource } from "@angular/material/table";
import { IPermissionTypeModel } from "app/attendance-setup/models/permissions";
import { PermissionTypeService } from "app/attendance-setup/services/permissions.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
	selector: "app-permission-index",
	templateUrl: "./permission-index.component.html",
	styleUrls: ["./permission-index.component.scss"],
})
export class PermissionIndexComponent extends AppHelpers implements OnInit {
	dataSource: MatTableDataSource<IPermissionTypeModel>;
	isLoaded: boolean = false;
	mandatoryColomns: string[] = [
		"Ser",
		"id",
		"name",
		"num_Permission_Per_Month",
		"num_Permission_Per_Year",
		"enable_Validation",
		"actions",
	];
	optionalColomns: string[] = [
		"warningInsertAction",
		"stopInsertAction",
		"total_Hour_Per_Permission",
		"total_Hour_Per_Month",
	];
	constructor(private permissionService: PermissionTypeService) {
		super();
	}

	ngOnInit(): void {
		// if (this.CheckNoBusinessOrCompany()) {
		//   this.getList(this.page.pageIndex);
		// }
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.checkNoBusinessOrCompany())
			this.getList(this.pageFromUrl > 0 ? this.pageFromUrl : this.firstPage);
	}

	getList(page: number, pageSize?: number) {
		this.page.pageIndex = page;
		if (pageSize) this.page.pageSize = pageSize;
		this.removePageParams();
		let params: IPageParams = {
			PageNumber: page,
			pageSize: this.page.pageSize,
		};
		this.permissionService
			.getList(params)
			.subscribe((res: IResponseList<IPermissionTypeModel[]>) => {
				if (res.message.messageType == MessageTypes.Success) {
					if (res.data.length == 0 && page > 1) {
						this.getList(page - 1);
						return;
					}
					this.dataSource = new MatTableDataSource(res.data);
					this.isLoaded = true;
					this.page.length = res.meta.totalItemCount;
				} else this.errorList(res.message.messages);
			});
	}

	// navigateToEdit(permission: IPermissionTypeModel) {
	//   this.router.navigate(
	//     [`attendance-setup/${this.moduleId}/edit-permission/${permission.id}`],
	//     {
	//       queryParams: {
	//         parentView: this.getViewName,
	//         ...this.pageParams,
	//       },
	//     }
	//   );
	// }

	onDelete(permission: IPermissionTypeModel): void {
		let msg = this.msgTranslate(permission.name);
		this.deleateDialog(msg).subscribe((confirm) => {
			if (confirm) this.delete(permission);
		});
	}

	delete(permission: IPermissionTypeModel) {
		this.showLoading();
		this.permissionService.Delete(permission.id).subscribe((res: IResponse) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
				if (this.dataSource.data.length == 1 && this.currentPage > 1)
					this.getList(this.prevPage, this.page.pageSize);
				else this.getList(this.currentPage, this.page.pageSize);
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}
}
