import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import { IPenaltyModel } from "../models/attendance-penalty-setup";
import { IResponse } from "app/shared/interfaces/responces";

@Injectable({
	providedIn: "root",
})
export class AttendancePenaltySetupService extends AppClientService {
	constructor() {
		super();
	}

	create(data: IPenaltyModel) {
		return this.post<IResponse<IPenaltyModel>>(
			"AttendanceSetup/AttendancePenaltyDeductionSetup/Create",
			data
		);
	}

	update(data: IPenaltyModel) {
		return this.post<IResponse<IPenaltyModel>>(
			"AttendanceSetup/AttendancePenaltyDeductionSetup/Edit",
			data
		);
	}

	getObject() {
		return this.get<IResponse<IPenaltyModel>>(
			"AttendanceSetup/AttendancePenaltyDeductionSetup/GetObject"
		);
	}
}
